import React, { useState } from 'react';
import './Clock.scss';

const ClockNew = () => {
	
	const setFullDate = () => {
		let date = new Date(),
			fulldate = date.toLocaleDateString('en-EN', {
				weekday: 'long',
				month: 'long',
				day: 'numeric',
			});
		return fulldate;
	};

	const setHours = () => {
		let date = new Date(),
			hours = date.getHours();
		return hours < 10 ? (hours = '0' + hours.toString()) : hours.toString();
	};

	const setMinutes = () => {
		let date = new Date(),
			minutes = date.getMinutes();
		return minutes < 10 ? (minutes = '0' + minutes.toString()) : minutes.toString();
	};

	let [seconds, setSeconds] = useState('00');
	setInterval(() => {
		let date = new Date();
		seconds = date.getSeconds();
		setSeconds(seconds < 10 ? (seconds = '0' + seconds.toString()) : seconds.toString());
	}, 1000);

	return (
		<div className='timedate'>
			<div className='caption'>Today is</div>
			<div className='full-date'>{setFullDate()}</div>
			<hr />
			<div className='time'>
				<div className='hours'>{setHours()}</div>
				<div className='minutes'>{setMinutes()}</div>
				<div className='seconds'>{seconds}</div>
			</div>
		</div>
	);
};

export default ClockNew;
